import {extend} from "flarum/extend";
import AdminNav from "flarum/components/AdminNav";
import AdminLinkButton from "flarum/components/AdminLinkButton";
import AdsPage from './components/AdsPage';

export default function () {
    // create the route
    app.routes['flarum-ads'] = {path: '/flarum/ads', component: AdsPage.component()};

    // bind the route we created to the three dots settings button
    app.extensionSettings['flarum-ads'] = () => m.route(app.route('flarum-ads'));

    extend(AdminNav.prototype, 'items', items => {
        items.add('flarum-ads', AdminLinkButton.component({
            href: app.route('flarum-ads'),
            icon: 'fas fa-dollar-sign',
            children: 'Ads',
            description: app.translator.trans('flarum-ads.admin.tab.description')
        }));
    });
}
